<template>
    <p-card>
        <template #content>
            <div class="p-fluid p-formgrid p-grid">
                <div class="p-field p-col-12 p-md-12">
                    <label for="cycle-type" class="p-mb-1">
                        Report Type
                    </label>
                    <p-dropdown
                        v-model="form.reportType"
                        :options="reportTypes"
                        optionLabel="label"
                        optionValue="value"
                        @change="updateReportType"
                    />
                </div>
                <div
                    class="p-field p-col-12 p-md-6"
                    v-if="form.reportType !== 'semen'"
                >
                    <label for="cycle-type" class="p-mb-1">
                        Cycle Type
                    </label>
                    <p-multi-select
                        v-model="form.cycleTypes"
                        :options="cycleTypesOpntions"
                        optionLabel="cycleType"
                        optionValue="cycleType"
                    />
                </div>

                <div
                    class="p-field p-col-12 p-md-6"
                    v-if="['cycle', 'kpiC'].includes(form.reportType)"
                >
                    <label class="p-mb-1"> Funded </label>
                    <p-dropdown
                        v-model="form.funded"
                        :options="fundedOptions"
                        optionLabel="label"
                        optionValue="value"
                    />
                </div>
                <div
                    class="p-field p-col-12 p-md-6"
                    v-if="['cycle', 'kpiC'].includes(form.reportType)"
                >
                    <label class="p-mb-1">
                        Cancel
                    </label>
                    <p-dropdown
                        v-model="form.cancelled"
                        :options="cancelOptions"
                        optionLabel="label"
                        optionValue="value"
                    />
                </div>

                <div
                    class="p-field p-col-12 p-md-6"
                    v-if="form.reportType == 'semen'"
                >
                    <label for="cycle-canceled" class="p-mb-1">
                        Sperm Donor
                    </label>
                    <p-dropdown
                        v-model="form.spermDonor"
                        :options="spermDonorOptions"
                        optionLabel="label"
                        optionValue="value"
                    />
                </div>

                <div
                    class="p-field p-col-12 p-md-6"
                    v-if="form.reportType !== 'patientVisit'"
                >
                    <label for="cycle-canceled" class="p-mb-1">
                        Location
                    </label>
                    <p-dropdown
                        v-model="form.siteId"
                        :options="locationOptions"
                        optionLabel="name"
                        optionValue="id"
                    />
                </div>

                <div
                    class="p-field p-col-12 p-md-6"
                    v-if="form.reportType == 'patientVisit'"
                >
                    <label class="p-mb-1"> Funded </label>
                    <p-dropdown
                        v-model="form.funded"
                        :options="fundedOptions"
                        optionLabel="label"
                        optionValue="value"
                    />
                </div>

                <div class="p-field p-col-12 p-md-6">
                    <label class="p-mb-1">From Date</label>
                    <p-calendar
                        :showIcon="true"
                        v-model="form.from"
                        dateFormat="yy-mm-dd"
                        :class="{
                            'p-invalid': submitted && v$.from.$invalid
                        }"
                    />
                </div>
                <div class="p-field p-col-12 p-md-6">
                    <label class="p-mb-1">To Date</label>
                    <p-calendar
                        :showIcon="true"
                        v-model="form.to"
                        dateFormat="yy-mm-dd"
                        :class="{
                            'p-invalid': submitted && v$.to.$invalid
                        }"
                    />
                </div>
                <!-- <div
                    class="p-field p-col-12 p-md-12"
                    v-if="form.reportType !== 'patientVisit'"
                >
                    <label for="cycle-type" class="p-mb-1">
                        Cycle Doctor
                    </label>
                    <p-multi-select
                        v-model="form.cycleDoctor"
                        :options="cycleDcotorOptions"
                        :filter="true"
                        optionLabel="name"
                        optionValue="id"
                    />
                </div> -->
                <div class="p-field p-col-12 p-md-12">
                    <label for="cycle-type" class="p-mb-1">
                        Cycle Doctor
                    </label>
                    <p-multi-select
                        v-model="form.demographicDoctor"
                        :options="demographicDcotorOptions"
                        :filter="true"
                        optionLabel="name"
                        optionValue="id"
                    />
                </div>
                <div
                    class="p-field p-col-12 p-md-12 button-container"
                    v-if="!['kpiC'].includes(form.reportType)"
                >
                    <p-button
                        label="Get Report"
                        @click="getCycleReport"
                        class="custom-button"
                    />
                    <p-button
                        label="Export XLS"
                        @click="getReportExcel"
                        :loading="loading"
                        class="custom-button"
                    />
                </div>
            </div>
            <hr
                class="p-mt-2 p-mb-2"
                v-if="!['kpiC'].includes(form.reportType)"
            />
            <div
                class="p-fluid p-formgrid p-grid"
                v-if="['cycle'].includes(form.reportType)"
            >
                <div class="p-field p-col-12 p-md-6">
                    <label for="cycle-type" class="p-mb-1">
                        Cycle Type
                    </label>
                    <p-multi-select
                        v-model="form2.cycleTypes"
                        :options="cycleTypesOpntions"
                        optionLabel="cycleType"
                        optionValue="cycleType"
                    />
                </div>

                <div class="p-field p-col-12 p-md-6">
                    <label for="cycle-canceled" class="p-mb-1">
                        Location
                    </label>
                    <p-dropdown
                        v-model="form2.siteId"
                        :options="locationOptions"
                        optionLabel="name"
                        optionValue="id"
                        :class="{
                            'p-invalid': submitted2 && v2$.siteId.$invalid
                        }"
                    />
                </div>

                <div class="p-field p-col-12 p-md-6">
                    <label class="p-mb-1">Date</label>
                    <p-calendar
                        :showIcon="true"
                        v-model="form2.date"
                        dateFormat="yy-mm-dd"
                        :class="{
                            'p-invalid': submitted2 && v2$.date.$invalid
                        }"
                    />
                </div>

                <div class="p-field p-col-12 p-md-12 button-container">
                    <p-button
                        label="Get Report"
                        @click="getCycleDailyReport"
                        class="custom-button"
                    />
                    <p-button
                        label="Export XLS"
                        @click="getDailyReportExcel"
                        :loading="loading2"
                        class="custom-button"
                    />
                </div>
            </div>
            <hr
                class="p-mt-2 p-mb-2"
                v-if="['cycle'].includes(form.reportType)"
            />
            <div
                class="p-fluid p-formgrid p-grid"
                v-if="['cycle', 'kpiC'].includes(form.reportType)"
            >
                <div
                    class="p-field p-col-12 p-md-6"
                    v-if="['cycle', 'kpiC'].includes(form.reportType)"
                >
                    <label for="cycle-type" class="p-mb-1">
                        Search In Comments
                    </label>
                    <p-input-text v-model="form.comments" />
                </div>

                <div
                    class="p-field p-col-12 p-md-6"
                    v-if="['cycle', 'kpiC'].includes(form.reportType)"
                >
                    <label class="p-mb-1"> Pregnancy </label>
                    <p-dropdown
                        v-model="form.pregnancy"
                        :showClear="true"
                        :options="pregnancyOptions"
                        optionLabel="label"
                        optionValue="value"
                    />
                </div>
                <div
                    class="p-field p-col-12 p-md-6"
                    v-if="['cycle', 'kpiC'].includes(form.reportType)"
                >
                    <label class="p-mb-1"> Protocol </label>
                    <p-multi-select
                        v-model="form.protocol"
                        :showClear="true"
                        :options="protocolOptions"
                        optionLabel="label"
                        optionValue="value"
                    />
                </div>
                <div
                    class="p-field p-col-12 p-md-6"
                    v-if="['cycle', 'kpiC'].includes(form.reportType)"
                >
                    <label class="p-mb-1">Drug</label>
                    <p-multi-select
                        v-model="form.drug"
                        :options="drugOptions"
                        optionLabel="name"
                        optionValue="name"
                    />
                </div>
                <div
                    class="p-field p-col-12 p-md-6"
                    v-if="['cycle', 'kpiC'].includes(form.reportType)"
                >
                    <label class="p-mb-1">Trigger</label>
                    <p-multi-select
                        v-model="form.trigger"
                        :options="triggerOptions"
                        optionLabel="description"
                        optionValue="name"
                    />
                </div>
                <div
                    class="p-field p-col-12 p-md-6"
                    v-if="['cycle', 'kpiC'].includes(form.reportType)"
                >
                    <label class="p-mb-1">Fert</label>
                    <p-multi-select
                        v-model="form.fert"
                        :options="fertOptions"
                        optionLabel="label"
                        optionValue="value"
                    />
                </div>
                <div
                    class="p-field p-col-12 p-md-6"
                    v-if="['cycle', 'kpiC'].includes(form.reportType)"
                >
                    <label class="p-mb-1">Diagnosis</label>
                    <p-multi-select
                        v-model="form.diagnosis"
                        :options="diagnosisOptions"
                        optionLabel="label"
                        optionValue="value"
                    />
                </div>
                <div
                    class="p-field p-col-12 p-md-6"
                    v-if="['cycle', 'kpiC'].includes(form.reportType)"
                >
                    <label class="p-mb-1">Incubator</label>
                    <p-multi-select
                        v-model="form.incubator"
                        :options="incubatorOptions"
                    />
                </div>
                <div
                    class="p-field p-col-12 p-md-12"
                    v-if="['cycle', 'kpiC'].includes(form.reportType)"
                >
                    <label class="p-mb-1">Embryologist doing ICSI/IVF</label>
                    <p-multi-select
                        v-model="form.embISCI"
                        :options="embryologistOptions"
                        optionLabel="name"
                        optionValue="id"
                    />
                </div>
                <div
                    class="p-field p-col-12 p-md-12"
                    v-if="['cycle', 'kpiC'].includes(form.reportType)"
                >
                    <label class="p-mb-1">Embryologist doing biopsy</label>
                    <p-multi-select
                        v-model="form.embBiopsy"
                        :options="embryologistOptions"
                        optionLabel="name"
                        optionValue="id"
                    />
                </div>
                <div
                    class="p-field p-col-12 p-md-12"
                    v-if="['cycle', 'kpiC'].includes(form.reportType)"
                >
                    <label class="p-mb-1">Embryologist doing Freezing</label>
                    <p-multi-select
                        v-model="form.embFreezing"
                        :options="embryologistOptions"
                        optionLabel="name"
                        optionValue="id"
                    />
                </div>
                <div
                    class="p-field p-col-12 p-md-12"
                    v-if="['cycle', 'kpiC'].includes(form.reportType)"
                >
                    <label class="p-mb-1">Embryologist doing Thawing</label>
                    <p-multi-select
                        v-model="form.embThawing"
                        :options="embryologistOptions"
                        optionLabel="name"
                        optionValue="id"
                    />
                </div>
                <div
                    class="p-field p-col-12 p-md-12"
                    v-if="['cycle', 'kpiC'].includes(form.reportType)"
                >
                    <label class="p-mb-1">Embryologist doing Transfer</label>
                    <p-multi-select
                        v-model="form.embTransfer"
                        :options="embryologistOptions"
                        optionLabel="name"
                        optionValue="id"
                    />
                </div>
                <div
                    class="p-field p-col-12 p-md-12"
                    v-if="['cycle', 'kpiC'].includes(form.reportType)"
                >
                    <label class="p-mb-1">Maturity</label>
                    <p-multi-select
                        v-model="form.maturity"
                        :options="maturityOptions"
                        optionLabel="label"
                        optionValue="value"
                    />
                </div>
                <div
                    class="p-field p-col-12 p-md-6 button-container p-mt-1"
                    v-if="['cycle'].includes(form.reportType)"
                >
                    <p-button
                        label="Get List"
                        @click="getCycleReport"
                        class="custom-button"
                    />
                </div>

                <div
                    class="p-field p-col-12 p-md-12 button-container"
                    v-if="['kpiC'].includes(form.reportType)"
                >
                    <p-button
                        label="Get Data"
                        @click="getCycleKpidata"
                        class="custom-button"
                    />
                    <p-button
                        label="Export Data"
                        @click="exportCycleKpidata"
                        :loading="loading"
                        class="custom-button"
                    />
                </div>
            </div>
        </template>
    </p-card>
</template>

<script>
import { useCycleReportForm } from "@/composable/cycle-report";
import { emitter } from "@/event-bus";
import { onMounted, watch } from "vue";
import { computed, inject, ref } from "vue";
import store from "@/store";
import { $axios } from "@/service/app-http";
import { ORIGIN } from "@/config";

export default {
    setup() {
        const {
            form,
            form2,
            cycleTypesOpntions,
            fundedOptions,
            reportTypes,
            spermDonorOptions,
            cancelOptions,
            locationOptions,
            cycleDcotorOptions,
            demographicDcotorOptions,
            getCycleReport,
            getCycleKpidata,
            getCycleKpipayload,
            getCycleDailyReport,
            getAllCycleTypes,
            getLocationOption,
            submitted,
            submitted2,
            v$,
            v2$,
            getReportExcel,
            getDailyReportExcel,
            updateReportType,
            loading,
            loading2
        } = useCycleReportForm();

        const pregnancyOptions = ref([
            { label: "Positive", value: 1 },
            { label: "Negative", value: 0 }
        ]);

        const protocolOptions = ref([
            { label: "Natural cycle", value: "Natural Cycle" },
            {
                label: "Modified natural cycle",
                value: "Modified natural cycle"
            },
            { label: "Mild Stimulation", value: "Mild Stimulation" },
            { label: "Antagonist", value: "Antagonist" },
            { label: "Long agonist", value: "Long agonist" },
            { label: "Flare agonist", value: "Flare agonist" },
            {
                label: "Microdose flare agonist",
                value: "Microdose flare agonist"
            },
            { label: "IVM protocol", value: "IVM protocol" },
            { label: "High Estrace", value: "High Estrace" },
            { label: "Regular Estrace", value: "Regular Estrace" },
            { label: "Other", value: "Other" }
        ]);

        const inventoryList = computed(
            () => store.state.cycleMonitoring.inventoryList
        );

        const defaultDiagnosisOptions = computed(() => {
            return diagnosisOptions.value
                ? diagnosisOptions.value.map((option) => option.value)
                : [];
        });

        const defaultDoctors = computed(() => {
            return demographicDcotorOptions.value
                ? demographicDcotorOptions.value.map((option) => option.id)
                : [];
        });

        // const triggerOptions = ref([
        //     { label: "HCG", value: "hcg" },
        //     { label: "Ovidrel", value: "ovidrel" },
        //     { label: "Lupron", value: "lupron" },
        //     { label: "Suprefact", value: "suprefact" }
        // ]);

        const triggerOptions = computed(() => [
            ...store.state.cycleMonitoring.inventoryList.filter(
                (item) => item.code === "Trigger"
            ),
            { name: EMPTY_DRUG_OPTION }
        ]);

        const fertOptions = ref([
            { label: "ICSI", value: "icsi" },
            { label: "Split IVF/ICSI", value: "split_ivf_icsi" },
            { label: "IVF", value: "ivf" }
        ]);

        const diagnosisOptions = computed(
            () => store.state.options.reasonsForTreatmentCycle
        );

        const EMPTY_DRUG_OPTION = "- Empty -";

        const drugOptions = computed(() => [
            ...store.state.cycleMonitoring.inventoryList.filter(
                (item) => item.code === "Stimulation" || item.code === "Luteal"
            ),
            { name: EMPTY_DRUG_OPTION }
        ]);

        const defaultDrugValues = computed(() => {
            return drugOptions.value
                ? drugOptions.value.map((option) => option.name)
                : [];
        });

        const exportCycleKpidata = async () => {
            const paramPayload = getCycleKpipayload();
            const { data } = await $axios.post(
                `cycle-report/export-cycle-pregnancy-report`,
                paramPayload
            );

            console.log(data);

            window.open(`${ORIGIN}/${data.file}`);
        };

        // watch(
        //     defaultDrugValues,
        //     (newVal) => {
        //         form.drug = newVal;
        //     },
        //     { immediate: true }
        // );

        const incubatorOptions = [
            "Minc 1",
            "Minc 2",
            "Minc 3",
            "Planer 1",
            "Planer 2",
            "Planer 3",
            "Planer 4",
            "Ksystem 1",
            "Ksystem 2",
            "Astec"
        ];

        const embryologistOptions = computed(
            () => store.state.doctor.embryologists
        );

        const defaultSelectedValues = computed(() => {
            return embryologistOptions.value
                ? embryologistOptions.value.map((option) => option.id)
                : [];
        });

        const maturityOptions = computed(
            () => store.state.options.maturityList
        );

        const defaultMaturityValues = computed(() => {
            return maturityOptions.value
                ? maturityOptions.value.map((option) => option.value)
                : [];
        });

        // watch(
        //     defaultSelectedValues,
        //     (newVal) => {
        //         form.embISCI = newVal;
        //         form.embBiopsy = newVal;
        //     },
        //     { immediate: true }
        // );

        // watch(
        //     defaultMaturityValues,
        //     (newVal) => {
        //         form.maturity = newVal;
        //     },
        //     { immediate: true }
        // );

        // watch(
        //     defaultDiagnosisOptions,
        //     (newVal) => {
        //         form.diagnosis = newVal;
        //     },
        //     { immediate: true }
        // );

        watch(
            defaultDoctors,
            (newVal) => {
                form.demographicDoctor = newVal;
            },
            { immediate: true }
        );

        const cycleDoctorChangeListener = ({ value }) => {
            emitter.emit("cycle-report-form:cyecle-doctor-change", value);
        };

        const demograhicDoctorChangeListener = ({ value }) => {
            emitter.emit("cycle-report-form:demographic-doctor-change", value);
        };

        onMounted(() => {
            getAllCycleTypes();
            getLocationOption();
            if (inventoryList.value.length == 0) {
                store.dispatch("cycleMonitoring/getInventoryList");
            }

            form.cycleTypes = ["IVF", "FET"];
            form.funded = -1;
            form.cancelled = "false";
            form.siteId = -1;
            // form.incubator = incubatorOptions;
            // form.trigger = triggerOptions.value.map((option) => option.value);
            // form.fert = fertOptions.value.map((option) => option.value);
            //form.protocol = protocolOptions.value.map((option) => option.value);
        });

        return {
            pregnancyOptions,
            protocolOptions,
            loading2,
            form,
            form2,
            cycleTypesOpntions,
            fundedOptions,
            reportTypes,
            spermDonorOptions,
            cancelOptions,
            locationOptions,
            cycleDcotorOptions,
            demographicDcotorOptions,
            getCycleReport,
            getCycleKpidata,
            exportCycleKpidata,
            getCycleDailyReport,
            submitted,
            submitted2,
            v$,
            v2$,
            getReportExcel,
            getDailyReportExcel,
            updateReportType,
            loading,
            cycleDoctorChangeListener,
            demograhicDoctorChangeListener,
            drugOptions,
            triggerOptions,
            fertOptions,
            embryologistOptions,
            defaultSelectedValues,
            incubatorOptions,
            diagnosisOptions,
            maturityOptions,
            defaultMaturityValues,
            defaultDiagnosisOptions
        };
    }
};
</script>

<style scoped>
.button-container {
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.custom-button {
    flex: 1;
    margin-right: 1rem;
    height: 34px;
    line-height: 40px;
    padding: 0;
}

.custom-button:last-child {
    margin-right: 0;
}
</style>
