<template>
    <div class="cycle-report-page">
        <!-- Show loading spinner once when loading is true -->
        <div v-if="loading" class="loading-container">
            <p-progress-spinner />
        </div>

        <!-- Show data only when not loading -->
        <div v-else>
            <!-- Loop through each item in the gData array -->
            <div
                v-for="(dataItem, index) in gData"
                :key="index"
                class="data-item"
                style="margin-bottom: 50px;"
            >
                <!-- Title with doctorName -->
                <h2>{{ dataItem.doctorName }}</h2>

                <!-- Grid structure to show left and right panes side by side -->
                <div class="row">
                    <!-- Left Pane (75%) -->
                    <div style="padding: 20px;">
                        <div>
                            <!-- Knobs on top -->
                            <div class="knobs-container">
                                <div v-if="dataItem.HCGgt10_perc">
                                    <p-knob
                                        v-model="dataItem.HCGgt10_perc"
                                        :readonly="true"
                                        :size="120"
                                        :value-color="'#FFA726'"
                                    />
                                    <span class="knob-label">HCG > 10%</span>
                                </div>
                                <div v-if="dataItem.GS_perc">
                                    <p-knob
                                        v-model="dataItem.GS_perc"
                                        :readonly="true"
                                        :size="120"
                                        :value-color="'#42A5F5'"
                                    />
                                    <span class="knob-label">GS %</span>
                                </div>

                                <div v-if="dataItem.HB_perc">
                                    <p-knob
                                        v-model="dataItem.HB_perc"
                                        :readonly="true"
                                        :size="120"
                                        :value-color="'#66BB6A'"
                                    />
                                    <span class="knob-label">HCG > 10%</span>
                                </div>
                                <div v-if="dataItem.Loss_perc">
                                    <p-knob
                                        v-model="dataItem.Loss_perc"
                                        :readonly="true"
                                        :size="120"
                                        :value-color="'#EF5350'"
                                    />
                                    <span class="knob-label">Loss %</span>
                                </div>

                                <div v-if="dataItem.Fert_Rate_perc">
                                    <p-knob
                                        v-model="dataItem.Fert_Rate_perc"
                                        :readonly="true"
                                        :size="120"
                                        :value-color="'#e100f3'"
                                    />
                                    <span class="knob-label">Fert Rate %</span>
                                </div>
                                <div v-if="dataItem.Blast_Rate_perc">
                                    <p-knob
                                        v-model="dataItem.Blast_Rate_perc"
                                        :readonly="true"
                                        :size="120"
                                        :value-color="'#5f40fc'"
                                    />
                                    <span class="knob-label">Blast Rate %</span>
                                </div>
                            </div>

                            <!-- Chart below knobs -->
                            <div class="chart-container-1">
                                <Bar
                                    :chart-data="
                                        generateChartData(dataItem, 'left')
                                    "
                                    :options="chartOptions"
                                />
                            </div>
                        </div>
                    </div>
                </div>
                <!-- Add spacing between rows -->
                <div class="row-separator"></div>
            </div>
        </div>
    </div>
</template>

<script>
import { useCycleReportTable } from "@/composable/cycle-report";
import { computed, onMounted, onUnmounted } from "vue";
import Knob from "primevue/knob";
import { emitter } from "@/event-bus";
import { Bar } from "vue-chartjs";
import {
    Chart as ChartJS,
    Title,
    Tooltip,
    Legend,
    BarElement,
    CategoryScale,
    LinearScale
} from "chart.js";

ChartJS.register(
    Title,
    Tooltip,
    Legend,
    BarElement,
    CategoryScale,
    LinearScale
);

export default {
    components: {
        "p-knob": Knob,
        Bar
    },
    setup() {
        const { store, getCycleKPIC, loading } = useCycleReportTable();
        const gData = computed(() => store.state.cycleReport.gData);

        const generateChartData = (dataItem, pane) => {
            return {
                labels: [
                    "HCG > 10%",
                    "GS%",
                    "HB%",
                    "Loss%",

                    "",
                    "Fert Rate",
                    "Blast Rate"
                ],
                datasets: [
                    {
                        label: "Total Cycles : " + dataItem.totalCycles,
                        data: [
                            dataItem.HCGgt10_perc,
                            dataItem.GS_perc,
                            dataItem.HB_perc,
                            dataItem.Loss_perc,
                            0,
                            dataItem.Fert_Rate_perc,
                            dataItem.Blast_Rate_perc
                        ],
                        backgroundColor: [
                            "#FFA726",
                            "#42A5F5",
                            "#66BB6A",
                            "#EF5350",

                            "#e100f3",
                            "#5f40fc"
                        ],
                        borderColor: [
                            "#FB8C00",
                            "#1E88E5",
                            "#43A047",
                            "#E53935",

                            "#2d0030",
                            "#140078"
                        ],
                        borderWidth: 1
                    }
                ]
            };

            // if (pane === "left") {
            // } else if (pane === "right") {
            //     return {
            //         labels: ["Fert Rate", "Blast Rate"],
            //         datasets: [
            //             {
            //                 label: "KPI-C",
            //                 data: [
            //                     dataItem.Fert_Rate_perc,
            //                     dataItem.Blast_Rate_perc
            //                 ],
            //                 backgroundColor: ["#FFA726", "#42A5F5"],
            //                 borderColor: ["#FB8C00", "#1E88E5"],
            //                 borderWidth: 1
            //             }
            //         ]
            //     };
            // }
        };

        const chartOptions = {
            responsive: true,
            scales: {
                y: {
                    beginAtZero: true,
                    max: 100
                }
            }
        };

        onMounted(() => {
            getCycleKPIC();
            emitter.on("cycle-report-table:get-cycle-kpic", getCycleKPIC);
        });

        onUnmounted(() => {
            emitter.off("cycle-report-table:get-cycle-kpic", getCycleKPIC);
        });

        return {
            gData,
            generateChartData,
            loading,
            chartOptions
        };
    }
};
</script>

<style scoped>
/* Ensure the knob container is centered */

.knobs-container {
    display: flex;
    gap: 30px;
    justify-content: flex-start; /* Align knobs to the left */
    margin-bottom: 20px;
}

/* Adjusting the chart size */
.chart-container-1 {
    width: 500px;
    margin-top: 20px;
}

/* Knob label positioning */
.knob-label {
    display: block;
    text-align: center;
    margin-top: 10px;
}

/* Layout adjustments */
.row {
    display: flex;
    justify-content: space-between;
    gap: 20px; /* Add some space between the left and right panes */
}

/* Loading spinner container for centering */
.loading-container {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100vh;
}

/* Add spacing between rows */
.row-separator {
    height: 30px;
}

.cycle-report-page {
    padding-left: 50px;
}
</style>
